			grid-template-columns: 30% 30% 40%;
			align-items: center;
			column-gap: 15px;
		}

		td,
		th {
			padding: 0 7.5px;
			word-break: break-all;
		}

		tbody {
			border: 1px solid var(--light-gray);
			border-bottom: 0;

			tr {
				padding: 30px 0;
				border-bottom: 1px solid var(--light-gray);
			}
		}

		tfoot {
			padding-top: 30px;

			tr {
				display: block;
			}

			display: flex;
			justify-content: flex-end;
		}

		li {
			display: flex;

			p:first-child {
				margin-right: 10px;
			}
		}
	}

	.cart-pre-summary {
		padding-top: 30px;
		padding-bottom: 80px;

		li {
			margin-bottom: 20px;
		}
	}

	.checkout-summary {
		border-left: 1px solid var(--light-gray);

		.cart-main {
			height: 100%;
		}
	}

	.checkout-navigation {
		border-top: 1px solid var(--light-gray);
		display: grid;
		grid-template-columns: minmax(55%, 1fr) minmax(500px, 720px);

		.back-button,
		.cart__checkout-btn {
			padding: 40px;
		}

		.back-button span {
			display: flex;
			align-items: center;
		}

		.back-button svg {
			margin-right: 16px;
		}

		.cart__checkout-btn {
			text-align: center;
		}
	}
}